import Layout from '../components/Layout';
import Link from 'next/link';
import fetch from 'isomorphic-unfetch';

const Index = props => (
  <Layout>
    <h1>Ooze</h1>
    <ul>
      {props.posts.map(post => (
        <li>
        <Link as={`/p/${props.id}`} href={`post?title=${props.title}`}>
          <a>{props.title}</a>
        </Link>
      </li>
      ))}
    </ul>
  </Layout>
);

export default Index;